.header-text {
    font-family: 'Roboto';
    font-size: 2rem;
    font-weight: 700;
    letter-spacing: .2rem;
    text-align: center;
    justify-content: center;
    color: inherit;
    padding: 1vh 1vw;
}

.data {
    display: flex;
    justify-content: center;
    flex-direction: column;
}
.global-padding {
padding-left: 12%;
padding-right: 12%;
}

.grid-format {
display: grid;
direction: row;
justify-content: space-around;
align-items: center;
}

.home-section {
padding-top: 50px;
padding-bottom: 50px;
display: flex;
justify-content: space-between;
}

.title {
text-align: center;
}
    
.title-hero {
padding: 8px;
border-radius: 4px;
background-color: #bfe1d9;
color:black;
font-family: 'Roboto';
box-shadow: 0 0 5px rgba(0,0,0,0.5);
}

.elephant-img {
height: 100%;
width: 100%;
}

.data-section {
padding-bottom: 100px;
}

.data-hero {
font-family: 'Roboto';
text-align: center;
}

.data-text {
text-align: center;
border: 2px solid rgba(0, 0, 0, 0.4);
height: 100%;
min-height: 120px;
border-radius: 5px;
padding: 0px 8px 0px 8px;
}

.global-padding {
padding-left: 20%;
padding-right: 20%;
}
.table-container {
    align-items: center;
    font-family: 'Roboto';
    padding-left: 10px;
    padding-right: 10px;
    padding-bottom: 2%;
    text-align: center;
    align-items: center;
    display: inline;
    width: 100%;
}

.MuiDataGrid-root .MuiDataGrid-cell {
    white-space: normal !important;
    word-wrap: break-word !important;
    max-height: none !important;
  }
.center {
    display: flex;
    text-align: center;
    align-content: center;
    justify-content: center;
    padding: 20px;
    }
  
.button-header {
    display: flex;
    justify-content: space-between;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

